@import "core";

body {
  overflow-x: hidden;
  overflow-y: auto; }
a,button {
  &:focus {
    outline: 0; } }
a:hover {
  text-decoration: none; }
img {
  max-width: 100%; }
[data-toggle="map"] img {
  max-width: none; }
ul {
  list-style: none;
  padding: 0;
  margin: 0; }
@import "button";
@import "carousel";
@import "tabs";
@import "collapse";
@import "ueditor-style";
