// Credit: Nicolas Gallagher and SUIT CSS.
.img-bg {
  position: absolute !important;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0; }
.ratio {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;

  &::before {
    display: block;
    content: ""; }

  &-item,
  iframe,
  embed,
  object,
  video,
  img {
    @extend .img-bg; } }

.ratio-bg {
  background-size: cover;
  background-position: center center; }
.ratio-2-1 {
  &::before {
    padding-top: 50%; } }
.ratio-3-1 {
  &::before {
    padding-top: percentage(1 / 3); } }
.ratio-4-1 {
  &::before {
    padding-top: percentage(1 / 4); } }
.ratio-16-9 {
  &::before {
    padding-top: percentage(9 / 16); } }

.ratio-16-10 {
  &::before {
    padding-top: percentage(10 / 16); } }

.ratio-3-2 {
  &::before {
    padding-top: percentage(2 / 3); } }

.ratio-4-3 {
  &::before {
    padding-top: percentage(3 / 4); } }

.ratio-2-3 {
  &::before {
    padding-top: percentage(3 / 2); } }

.ratio-3-4 {
  &::before {
    padding-top: percentage(4 / 3); } }

.ratio-1-1 {
  &::before {
    padding-top: percentage(1 / 1); } }

