.collapse-v1 {
  margin-bottom: 1.5rem;
  .panel {
    margin-bottom: 0.5rem;
    border: 1px solid #ddd; }
  .panel-heading {
    background-color: #eee;
    border-bottom: 1px solid #DDD;

    h4 {
      font-size: 18px;
      margin-bottom: 0; }
    a {
      display: block;
      text-decoration: none;
      padding: 10px 15px;
      &:before {
        content: "-";
        float: right;
        line-height: 18px;
        margin-right: -4px;
        display: inline-block;
        font-size: 22px;
        @include size(18px);
        text-align: center; }
      &.collapsed {
        &:before {
          content: "+";
          font-size: 18px; } } } }
  .panel-body {
    padding: 12px 15px;

    p {
      margin-bottom: .5rem; } } }

