@font-face {
  font-family: 'decor';
  src: url('../fonts/decor/decor.eot?x6b2qs');
  src: url('../fonts/decor/decor.eot?x6b2qs#iefix') format("embedded-opentype"), url('../fonts/decor/decor.ttf?x6b2qs') format("truetype"), url('../fonts/decor/decor.woff?x6b2qs') format("woff"), url('../fonts/decor/decor.svg?x6b2qs#icomoon') format("svg");
  font-weight: normal;
  font-style: normal; }


i.dc {
  font-family: 'decor' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.dc-decor1:before {
  content: "\e900"; }

.dc-decor2:before {
  content: "\e901"; }

.dc-decor3:before {
  content: "\e902"; }
.ueditor-content {
  h1,h2,h3,h4,h5,h6,p {
    margin-bottom: 1.5rem; }
  ol,ul {
    margin-bottom: 1rem;
    padding-left: 40px; }
  ul {
    list-style: disc; }
  a:hover {
    text-decoration: underline; } }
.ueheading {
  padding: 30px 0 15px;
  line-height: 1.5;
  i {
    display: none; }
  h3 {
    color: inherit;
    font-size: 24px;
    display: inline-block; }
  span {
    font-size: inherit;
    line-height: inherit; }
  &.v1 {
    text-align: left;
    i {
      margin-right: 5px;
      &:first-child {
        display: inline-block; } } }
  &.v2, &.v3, &.v4 {
    text-align: center; }
  &.v2 {
    i {
      display: inline-block; }
    h3 {
      &::after, &::before {
        content: "";
        display: inline-block;
        width: 80px;
        border-top: 1px solid;
        opacity: 0.25; } } }
  &.v3 i, &.v4 i {
    font-size: 40px;
    line-height: 18px;
    opacity: 0.5; }
  &.v2 i {
    margin: 0 5px;
    font-size: 0.75em;
    vertical-align: middle; }
  &.v3 i:last-child, &.v4 i:last-child {
    margin-top: 8px; }
  &.v3 i:first-child, &.v4 i:first-child {
    margin-bottom: 8px; }
  &.v3 i, &.v4 i {
    font-size: 40px;
    line-height: 18px; }
  &.v3 i:last-child {
    display: block; }
  &.v4 i {
    display: block;
    &:first-child {
      transform: rotate(180deg); } }
  &.c2 span {
    color: #222; }
  &.c3 {
    h3 {
      &::after, &::before {
        color: #222; } }
    i {
      color: #222; } } }
